.timeline-bard-simple-container {
    overflow-y: auto;
    position: relative;
    height: 100vh;
    max-width: 98%;

    border: 1px solid #ccc;
}

.tblScore, .tblScore th, .tblScore td {
    border: 1px solid #000;
}

.div-one-play {
    display: flex;
    flex-direction: row;
}
.div-svg-sidemenu {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.td-virtual-table {
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.td-inning, .td-score {
    text-align: center;
    background-color: rgb(23, 23, 109);
}
.td-inning {
    color: #999;
}
.td-score {
    font-size: large;
    font-weight: bold;
}
.td-player1 {
    color: #fff;
}
.td-player2 {
    color: #ff0;
}